      <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"addNewMarker()"</span>&gt;</span>add new marker<span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
