  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">zoom</span>: <span class="hljs-number">13</span>,
      <span class="hljs-attr">center</span>: [<span class="hljs-number">121.5273285</span>, <span class="hljs-number">31.21515044</span>],
      <span class="hljs-attr">markers</span>: [
        {
          <span class="hljs-attr">position</span>: [<span class="hljs-number">121.5273285</span>, <span class="hljs-number">31.21515044</span>]
