<template><section><h1>地图</h1>
<hr>
<h2>模板</h2>
<pre><code class="language-javascript">&lt;div id=<span class="hljs-string">"demoMap1"</span> <span class="hljs-class"><span class="hljs-keyword">class</span></span>=<span class="hljs-string">"demo-component"</span>&gt;
  <span class="xml"><span class="hljs-tag">&lt;<span class="hljs-name">amap</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"demo-map-1"</span> <span class="hljs-attr">:zoom</span>=<span class="hljs-string">"zoom"</span> <span class="hljs-attr">:dragEnable</span>=<span class="hljs-string">"dragEnable"</span> <span class="hljs-attr">:center</span>=<span class="hljs-string">"center"</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">amap</span>&gt;</span></span>
  &lt;div <span class="hljs-class"><span class="hljs-keyword">class</span></span>=<span class="hljs-string">"action-list"</span>&gt;
    <span class="xml"><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
      reactive prop : <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"changeZoom()"</span>&gt;</span>change zoom<span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span></span>
    &lt;p&gt;
      no reactive prop : <span class="xml"><span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"toggleDragEnable()"</span>&gt;</span>toggle dragEnable<span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span></span>
    &lt;<span class="hljs-regexp">/p&gt;
  &lt;/</span>div&gt;
<span class="xml"><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span></span>
</code></pre>
<h2>示例代码</h2>
<pre><code class="language-javascript"><span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">name</span>: <span class="hljs-string">'demoMap1'</span>,
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">zoom</span>: <span class="hljs-number">14</span>,
      <span class="hljs-attr">dragEnable</span>: <span class="hljs-literal">true</span>,
      <span class="hljs-attr">center</span>: [<span class="hljs-number">121.5273285</span>, <span class="hljs-number">31.21515044</span>]
    }
  },
  <span class="hljs-attr">methods</span>: {
    changeZoom () {
      <span class="hljs-keyword">this</span>.zoom++
